		getLanguage() {
			var l;
			switch (this.$i18n.locale) {
				case 'de':
					l = "Deutsch";
					break;
				case 'en':
					l = "English";
					break;
				case 'es':
					l = "Espaol";
					break;
				case 'fr':
					l = "Franais";
					break;
				case 'gr':
					l = "";
					break;
				case 'tr':
					l = "Trke";
					break;
				default:
					l = "-";
					break;
			}
			return l;
		},
		updateDarkmode() {
			var mode;

			switch (this.darkMode) {
				case 'off':
					mode = "light";
					break;
				case 'auto':
					if (window.matchMedia && window.matchMedia('(prefers-color-scheme: dark)').matches) {
						mode = "dark";
					} else {
						mode = "light";
					}
					break;
				case 'on':
					mode = "dark";
					break;
				default:
					mode = "dark";
					break;
			}

			event.preventDefault();
			appOption.appMode = mode;
			if (localStorage) {
				localStorage.appMode = appOption.appMode;
			}
			document.documentElement.setAttribute('data-bs-theme', mode);
			var newVariables = generateVariables();
			appVariable.font = newVariables.font;
			appVariable.color = newVariables.color;
		},
		updateColor() {
			var color;

			switch (this.appColor) {
				case 'gray':
					color = "gray";
					break;
				case 'green':
					color = "green";
					break;
				case 'purple':
					color = "purple";
					break;
				case 'orange':
					color = "orange";
					break;
				case 'blue':
					color = "blue";
					break;
				case 'red':
					color = "red";
					break;
				default:
					color = "pink";
					break;
			}

			event.preventDefault();

			appOption.appThemeClass = "theme-" + color;

			if (localStorage) {
				localStorage.appThemeClass = appOption.appThemeClass;
			}

			for (var x = 0; x < document.body.classList.length; x++) {
				var targetClass = document.body.classList[x];
				if (targetClass.search('theme-') > -1) {
					document.body.classList.remove(targetClass);
				}
			}

			document.body.classList.add("theme-" + color);

			var newVariables = generateVariables();
			appVariable.font = newVariables.font;
			appVariable.color = newVariables.color;
		},
		togglePWV: function (pEye, pPwf) {
